                <div class="source" slot="source"><template>
  <bee-table :data="data" :active-index="actived" @selectionChange="selectionChange">
    <bee-table-column width="50" type="selection"></bee-table-column>
    <bee-table-column width="100" label="" prop="name"></bee-table-column>
    <bee-table-column width="100" label="" prop="age"></bee-table-column>
    <bee-table-column width="100" label="" prop="grades" placeholder="-"></bee-table-column>
    <bee-table-column width="100" label="" prop="subject"></bee-table-column>
    <bee-table-column width="160" label="">
      <template slot-scope="scope">
        <span class="primary" @click="view(scope.row, scope.$index)"></span>
        <span class="primary" @click="delete(scope.row, scope.$index)"></span>
      </template>
    </bee-table-column>
  </bee-table>
</template>

</div>
                
